.header {
  position: relative;
  z-index: 100;

  &__wrapper {
    width: 100%;
    padding: 15px 15px 15px 70px;
    position: absolute;
    top: 0;
    left: 0;
  }

  &__inner {
    display: flex;
    align-items: flex-start;
    position: relative;
  }

  &__logo {
    display: inline-flex;

    img {
      height: 80px;
    }
  }

  &__elements {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    // position: absolute;
    position: fixed;
    top: 15px;
    right: 15px;
  }

  &__btn {
    display: flex;
    width: 68px;
    height: 68px;
    justify-content: center;
    align-items: center;
    color: $white;
    border: 0;
    background-color: $primary;
    padding: 6px;
    margin-left: auto;

    &-text {
      font-size: 8px;
      font-weight: 700;
      line-height: 1;
      text-align: center;
      position: relative;
      top: -1px;
    }

    &-bars {
      display: flex;
      flex-direction: column;
      justify-content: center;
      margin-left: 6px;

      span {
        display: block;
        width: 30px;
        height: 2px;
        background-color: #fff;
        margin: 2px 0;
      }
    }
  }

  &__link {
    display: inline-block;
    font-size: 14px;
    font-weight: 700;
    color: $white;
    padding: 12px 6px 12px 8px;
    text-transform: uppercase;
    writing-mode: vertical-lr;
    background-color: $primary;
    margin-top: 10px;

    &:hover {
      color: $white;
      text-decoration: none;
    }
  }

  &__nav {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 35px;
  }

  &__nav-link {
    color: $yellow;
    font-size: 16px;
    margin-top: 6px;
    text-shadow: 0 1px 2px rgba(#000, 0.5);

    &:hover {
      color: $yellow;
    }
  }

  @media (max-width: 767px) {
    &__wrapper {
      padding: 15px;
    }

    &__logo {
      img {
        height: 60px;
      }
    }

    &__btn {
      width: 50px;
      height: 50px;

      &-text {
        font-size: 8px;
        position: relative;
        top: -1px;
      }

      &-bars {
        margin-left: 4px;

        span {
          width: 20px;
          height: 2px;
          margin: 2px 0;
        }
      }
    }

    &__link {
      font-size: 10px;
      padding: 8px 5px 8px 7px;
      margin-top: 4px;
    }

    &__nav {
      display: flex;
      flex-direction: row;
      width: auto;
      position: absolute;
      top: 10px;
      right: 100%;
    }

    &__nav-link {
      margin: 0 10px 0 0;
    }
  }
}
