.menu {
  list-style: none;
  margin: 0;
  padding: 0;

  &-item {
    margin: 4px 0 8px;
    position: relative;
  }

  &-link {
    display: block;
    width: 100%;
    font-size: 14px;
    line-height: 40px;
    padding: 0 20px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    color: $primary;

    &:hover {
      color: $primary;
      text-decoration: none;
    }
  }

  &-toggle {
    display: inline-flex;
    width: 40px;
    height: 40px;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    right: 0;
    cursor: pointer;

    span {
      display: inline-flex;
      width: 24px;
      height: 24px;
      justify-content: center;
      align-items: center;
      border-radius: 12px;
      transition: 0.3s;

      &:hover {
        background-color: rgba(#000, 0.1);
      }
    }

    &.active span {
      transform: rotate(90deg);
    }
  }

  &-sub {
    display: none;
    background-color: $white;
    padding: 8px 0;
  }

  &-sub &-link {
    padding-left: 40px;
    color: $primary;
    text-transform: uppercase;
  }

  &-sub-2 &-link {
    padding-left: 60px;
  }

  &-sub-3 &-link {
    padding-left: 80px;
  }

  &-item:hover > &-link {
    background-color: rgba($primary, 0.05);
  }

  &-root > &-item > &-link {
    font-size: 14px;
    font-weight: 700;
    text-transform: uppercase;
    color: $yellow;
  }

  &-root > &-item:hover > &-link {
    background-color: rgba(#fff, 0.15);
  }

  &-root > &-item-group > &-toggle {
    color: $yellow;

    &:hover {
      span {
        background-color: rgba(#fff, 0.25);
      }
    }
  }
}
