.utilities {
  position: relative;
  z-index: 1;

  &__images {
    width: 50%;
    height: 100%;
    overflow: hidden;
    position: absolute;
    top: 0;
    left: 0;
  }

  &__frame {
    @include frame("shine");
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    transition: 0.3s;

    &.active {
      opacity: 1;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: 50% 50%;
      position: absolute;
      top: 0;
      left: 0;
    }
  }

  &__content {
    width: 50%;
    margin-left: auto;
    padding: 20px 124px 27px;
    position: relative;
    z-index: 1;
    overflow: hidden;
  }

  &__bg {
    max-width: 80%;
    max-height: 80%;
    object-fit: contain;
    object-position: 50% 50%;
    position: absolute;
    z-index: -2;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  &__list {
    list-style: none;
    margin: 50px 0 0;
    padding: 0;
  }

  &__link {
    display: inline-block;
    font-family: $belle;
    font-size: 26px;
    position: relative;
    transition: 0.3s;

    &::before {
      content: "";
      display: block;
      width: 14px;
      height: 14px;
      border-radius: 50%;
      background-color: $brown;
      position: absolute;
      top: 14px;
      left: -30px;
      opacity: 0;
      transition: 0.3s;
    }

    &.active,
    &:hover {
      color: $brown;
      text-decoration: none;
    }

    &.active::before,
    &:hover::before {
      opacity: 1;
    }
  }

  @media (max-width: 1500px) {
    &__content {
      padding: 60px 100px;
    }

    &__list {
      margin-top: 40px;
    }
  }

  @media (max-width: 1199px) {
    &__content {
      padding: 40px 80px;
    }

    &__link {
      font-size: 20px;

      &::before {
        width: 10px;
        height: 10px;
        top: 10px;
      }
    }
  }

  @media (max-width: 991px) {
    &__images {
      position: relative;
      height: 300px;
      width: 100%;
    }

    &__content {
      width: 100%;
      padding: 30px 60px 40px;
    }
  }

  @media (max-width: 575px) {
    &__content {
      padding: 40px 15px;
    }

    &__list {
      margin-top: 20px;
    }
  }
}

.grid {
  padding-top: 21px;
  background-color: rgba($primary, 0.1);

  &__body {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    margin-top: 30px;
  }

  &__item {
    position: relative;
    overflow: hidden;
  }

  &__frame {
    @include frame("rect", 56.25%);
  }

  &__overlay {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 20px;
    background-color: rgba($primary, 0.8);
    position: absolute;
    top: 26px;
    left: 26px;
    right: 26px;
    bottom: 26px;
    z-index: 10;
    opacity: 0;
    transform: translateY(20%);
    transition: 0.3s;

    &:hover {
      text-decoration: none;
    }
  }

  &__item:hover &__overlay {
    transform: translateY(0);
    opacity: 1;
  }

  &__title {
    font-size: 24px;
    font-weight: 400;
    text-transform: uppercase;
    text-align: center;
    color: $yellow;
    margin-bottom: 0;
  }

  &__footer {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $primary;
    padding: 27px 0;
  }

  &__btn {
    display: inline-block;
    font-size: 24px;
    text-transform: uppercase;
    font-weight: 700;
    line-height: 87px;
    padding: 0 58px;
    border: 1px solid $yellow;
    color: $yellow;

    &:hover {
      color: $yellow;
      text-decoration: none;
    }
  }

  @media (max-width: 1500px) {
    &__title {
      font-size: 18px;
    }

    &__btn {
      font-size: 18px;
      line-height: 50px;
      padding: 0 35px;
    }
  }

  @media (max-width: 991px) {
    &__body {
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: auto;
    }
  }

  @media (max-width: 575px) {
    &__body {
      margin-top: 20px;
    }

    &__overlay {
      top: 16px;
      left: 16px;
      right: 16px;
      bottom: 16px;
    }

    &__title {
      font-size: 16px;
    }

    &__btn {
      font-size: 14px;
      line-height: 35px;
      padding: 0 30px;
    }
  }
}

