.about {
  position: relative;

  &__frame {
    @include frame("rect", 80%);

    &::after {
      content: "";
      display: block;
      width: 100%;
      height: 20%;
      position: absolute;
      bottom: 0;
      left: 0;
      z-index: 10;
      background: linear-gradient(to bottom, rgba(#000, 0), rgba(#000, 0.4));
    }
  }

  &__overlay {
    display: flex;
    width: 100%;
    height: 100%;
    flex-direction: column;
    justify-content: flex-end;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    transition: 0.4s;

    &:hover {
      text-decoration: none;
      background-color: rgba($primary, 0.8);
    }
  }

  &__grow {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: 0.4s;
  }

  &__overlay:hover &__grow {
    flex-grow: 1;
  }

  &__title {
    font-family: $helveins;
    font-size: 40px;
    color: $yellow;
    text-transform: uppercase;
    text-align: center;
  }

  @media (max-width: 1199px) {
    &__title {
      font-size: 30px;
    }
  }
}

