  @media (max-width: 1199px) {
    .nav-link {
      font-size: 16px;
      padding: 10px 15px;
    }
  }

  @media (max-width: 767px) {
    padding: 15px;

    &__nav {
      display: block;
      text-align: center;
      white-space: nowrap;
      overflow: auto;
    }

    .nav-item {
      display: inline-block;

      &:not(:last-child) {
        margin-right: 6px;
      }
    }

    .nav-link {
      font-size: 14px;
      padding: 6px 12px;
    }
  }
}

.banner {
  &__img {
    display: block;
    width: 100%;
    min-height: 200px;
    object-fit: cover;
    object-position: 50% 50%;
  }
}

.life-style {
  &__frame {
    img {
      display: block;
      width: 100%;
    }

    &.is-fixed {
      @include frame("rect", 56.25%);
    }
  }

  &__content {
    font-size: 16px;
    line-height: 1.2;
    color: $primary;
  }

  @media (max-width: 767px) {
    &__content {
      font-size: 14px;
    }
  }
}

.apartment-detail {
  &__frame {
    img {
      display: block;
      width: 100%;
    }

    &.is-fixed {
      @include frame("rect", 50%);
    }
  }

  &__content {
    height: 100%;
    border-top: 1px solid $brown;
    border-bottom: 1px solid $brown;
    font-size: 16px;
    line-height: 1.2;
    color: $primary;
    padding: 20px 0;
  }

  &__space {
    height: 100%;
    border-bottom: 1px solid $brown;
    position: relative;
    top: -3px;
  }

  @media (max-width: 767px) {
    &__content {
      font-size: 14px;
    }
  }
}
