.footer {
  background-color: $primary;
  padding: 30px 0 0;

  &__inner {
    display: flex;
    flex-wrap: wrap;
  }

  &__block {
    flex: 0 0 50%;
    padding: 0 30px;

    &:last-child {
      border-left: 1px solid #999;
    }
  }

  &__content {
    max-width: 350px;
    margin: 0 auto;
  }

  &__logo {
    display: inline-flex;

    img {
      height: 90px;
    }
  }

  &__slogan {
    font-size: 16px;
    color: $border-color;
    text-align: center;
    margin-top: 4px;
  }

  &__title {
    font-size: 18px;
    font-weight: 700;
    text-transform: uppercase;
    color: $border-color;
    margin-bottom: 16px;
  }

  &__label {
    font-size: 20px;
    font-weight: 700;
    text-transform: uppercase;
    color: $border-color;
    margin-bottom: 8px;
  }

  &__contact {
    position: relative;
    color: #919191;
    font-size: 16px;
    padding-left: 30px;
    margin-top: 8px;

    & > .fa {
      position: absolute;
      top: 6px;
      left: 0;
      color: $yellow;
    }

    a,
    a:hover {
      color: inherit;
    }
  }

  &__copyright {
    margin-top: 70px;
    background-color: $black;
    color: #919191;
    text-align: center;
    font-size: 14px;
    padding: 8px 0;
  }

  @media (max-width: 767px) {
    &__inner {
      flex-direction: column;
    }

    &__block {
      &:last-child {
        border-left: 0;
        border-top: 1px solid #999;
        margin-top: 20px;
        padding-top: 20px;
      }
    }

    &__slogan {
      font-size: 16px;
    }

    &__title {
      font-size: 14px;
    }

    &__label {
      font-size: 18px;
    }

    &__contact {
      font-size: 16px;
    }

    &__copyright {
      font-size: 15px;
      padding: 12px 0;
    }
  }
}
