.image-slider {
  &__frame {
    display: block;

    img {
      display: block;
      width: auto;
      height: 100px;
    }
  }

  .swiper-slide {
    width: auto;
  }
}

.image-slider-2 {
  margin: 0 -4px;
  position: relative;

  &__frame {
    @include frame("rect", 65%);
    box-shadow: 0 1px 3px rgba(#000, 0.3);
  }

  &__prev,
  &__next {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    background-color: rgba($primary, 0.8);
    color: $white;
    font-size: 20px;
    line-height: 30px;
    cursor: pointer;
    position: absolute;
    z-index: 10;
    top: 50%;
    left: 4px;
    transform: translateY(-50%);
    opacity: 0;
    transition: 0.3s;

    &:focus {
      outline: 0;
    }
  }

  &__next {
    left: auto;
    right: 4px;
  }

  &:hover &__prev,
  &:hover &__next {
    opacity: 1;
  }

  .swiper-slide {
    padding: 4px;
  }
}

.banner-slider {
  &__frame {
    img {
      display: block;
      width: 100%;
    }
  }

  &__frame-2 {
    img {
      display: block;
      width: 100%;
      min-height: 200px;
      object-fit: cover;
      object-position: 50% 50%;
    }
  }
}

.news-slider {
  &__footer {
    display: flex;
    justify-content: flex-end;
    margin-top: 20px;
  }

  &__prev,
  &__next {
    display: flex;
    width: 14px;
    height: 24px;
    justify-content: center;
    align-items: center;
    font-size: 18px;
    cursor: pointer;

    &:focus {
      outline: 0;
    }
  }

  &__pagination {
    display: flex;

    .swiper-pagination-bullet {
      display: flex;
      width: 14px;
      height: 24px;
      justify-content: center;
      align-items: center;
      border-radius: 0;
      background: 0;
      opacity: 1;
      font-size: 18px;
      padding-bottom: 4px;
      margin: 0 3px;

      &:focus {
        outline: 0;
      }

      &-active {
        color: $yellow;
      }

      &::before {
        content: "1";
      }

      &:nth-child(2)::before {
        content: "2";
      }

      &:nth-child(3)::before {
        content: "3";
      }

      &:nth-child(4)::before {
        content: "4";
      }

      &:nth-child(5)::before {
        content: "5";
      }

      &:nth-child(6)::before {
        content: "6";
      }

      &:nth-child(7)::before {
        content: "7";
      }

      &:nth-child(8)::before {
        content: "8";
      }

      &:nth-child(9)::before {
        content: "9";
      }

      &:nth-child(10::before) {
        content: "10";
      }

      &:nth-child(11::before) {
        content: "11";
      }

      &:nth-child(12::before) {
        content: "12";
      }

      &:nth-child(13::before) {
        content: "13";
      }

      &:nth-child(14::before) {
        content: "14";
      }

      &:nth-child(15::before) {
        content: "15";
      }

      &:nth-child(16::before) {
        content: "16";
      }

      &:nth-child(17::before) {
        content: "17";
      }

      &:nth-child(18::before) {
        content: "18";
      }

      &:nth-child(19::before) {
        content: "19";
      }

      &:nth-child(20::before) {
        content: "20";
      }
    }
  }
}
