.section {
  padding: 80px 0;

  &__title {
    font-family: $helveins;
    font-size: 40px;
    font-weight: 400;
    text-transform: uppercase;
    text-align: center;
    color: $primary;
    margin-bottom: 80px;
  }

  &--bg {
    background: url("../images/background-image.png") no-repeat;
    background-size: cover;
    background-position: center center;
  }

  @media (max-width: 1199px) {
    padding: 60px;

    &__title {
      font-size: 30px;
      margin-bottom: 40px;
    }
  }

  @media (max-width: 767px) {
    padding: 40px 0;

    &__title {
      font-size: 24px;
      margin-bottom: 30px;
    }
  }
}

.section-2 {
  &__title {
    font-size: 32px;
    font-weight: 400;
    text-transform: uppercase;
    color: $primary;
    margin-bottom: 16px;
  }

  &--center &__title {
    text-align: center;
  }

  @media (max-width: 767px) {
    &__title {
      font-size: 24px;
    }
  }
}
